.album {
    width: 180px;
    height: 180px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    margin: 30px;
    -webkit-box-shadow: 0 3px 10px rgba(84, 84, 84, 0.747);
            box-shadow: 0 3px 10px rgba(84, 84, 84, 0.747);
    position: relative;
    cursor: pointer;
    -webkit-transition-duration: 250ms;
         -o-transition-duration: 250ms;
            transition-duration: 250ms;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
}

.album:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.album-info {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.4);
    color: white;
    overflow: hidden;
}

.album-info>p {
    margin: 0;
    padding: 10px;
    font-size: 14px;
}

.artist-albums {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}

.artist-page__info {
    position: relative;
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    z-index: 2;
    color: white;
    height: 90px;
    padding: 10px;
}

.artist-page__info__name {
    margin-bottom: 10px;
    font-size: 26px;
}

.artist-page {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}



